import { notification } from "antd";
import { RollbackOutlined, RedoOutlined, RestOutlined, EditOutlined, EyeOutlined, ImportOutlined, VerticalAlignTopOutlined, ExportOutlined, VerticalAlignBottomOutlined, CloseCircleOutlined, DeleteOutlined } from "@ant-design/icons";
import { FC, useMemo, DragEvent, useRef, MouseEvent, useState, ReactNode } from "react";
import { ReactVisualBlock } from "../components/ReactVisualBlock";
import { useCallbackRef } from "../hooks/useCallbackRef";
import { createVisualBlock, ReactVisualEditorBlock, ReactVisualEditorComponent, ReactVisualEditorConfig, ReactVisualEditorValue } from "../utils/editorUtils";
import { useVisualCommand } from "../utils/ReactVisualEditor.command";
import { createEvent } from "../plugin/event";
import classNames from "classnames";
import { $$dialog } from "../service/dialog/$$dialog";
import { $$dropdown, DropdownOption } from "../service/dropdown/dropdown";
import { BlockResizeDirection, ReactVisualBlockResize } from "../components/ReactVisualBlockResize";
import { ReactVisualEditorOperator } from "../components/ReactVisualOperator";
export const ReactVisualEditor: FC<{
  value: ReactVisualEditorValue;
  onChange: (val: ReactVisualEditorValue) => void;
  config: ReactVisualEditorConfig;
  formData: Record<string, any>;
  onFormDataChange: (FormData: Record<string, any>) => void;
}> = (props) => {
  const containerStyle = useMemo(() => {
    return {
      height: `${props.value.container.height}px`,
      width: `${props.value.container.width}px`,
    };
  }, [props.value.container.height, props.value.container.width]);
  const [preview, setPreview] = useState(false); //
  const [editing, setEditing] = useState(false); //
  const [selectIndex, setSelectIndex] = useState(-1);
  const selectBlock = useMemo(() => props.value.blocks[selectIndex] as ReactVisualEditorBlock | undefined, [props.value.blocks, selectIndex]);
  const [dragstart] = useState(() => createEvent());
  const [dragend] = useState(() => createEvent());
  // dom
  const containerRef = useRef({} as HTMLDivElement);
  const bodyRef = useRef({} as HTMLDivElement);
  const classes = useMemo(
    () =>
      classNames("react-visual-editor", {
        "react-visual-editor-preview": preview,
      }),
    [preview]
  );
  //
  const menuDragger = (() => {
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const dragData = useRef({
      dragComponent: null as null | ReactVisualEditorComponent,
    });
    const block = {
      // eslint-disable-next-line react-hooks/rules-of-hooks
      dragStart: useCallbackRef((e: DragEvent<HTMLDivElement>, dragComponent: ReactVisualEditorComponent) => {
        containerRef.current.addEventListener("dragenter", container.dragenter);
        containerRef.current.addEventListener("dragover", container.dragover);
        containerRef.current.addEventListener("dragleave", container.dragleave);
        containerRef.current.addEventListener("drop", container.drop);
        dragData.current.dragComponent = dragComponent;
        dragstart.emit();
      }),
      // eslint-disable-next-line react-hooks/rules-of-hooks
      dragend: useCallbackRef((e: DragEvent) => {
        containerRef.current.removeEventListener("dragenter", container.dragenter);
        containerRef.current.removeEventListener("dragover", container.dragover);
        containerRef.current.removeEventListener("dragleave", container.dragleave);
        containerRef.current.removeEventListener("drop", container.drop);
      }),
    };

    const container = {
      // eslint-disable-next-line react-hooks/rules-of-hooks
      dragenter: useCallbackRef((e: DragEvent) => {
        e.dataTransfer!.dropEffect = "move";
      }) as any,
      // eslint-disable-next-line react-hooks/rules-of-hooks
      dragover: useCallbackRef((e: DragEvent) => {
        e.preventDefault();
      }) as any,
      // eslint-disable-next-line react-hooks/rules-of-hooks
      dragleave: useCallbackRef((e: DragEvent) => {
        e.dataTransfer!.dropEffect = "none";
      }) as any,
      // eslint-disable-next-line react-hooks/rules-of-hooks
      drop: useCallbackRef((e: any) => {
        //DragEventoffsetY
        methods.updateBlocks([
          ...props.value.blocks,
          createVisualBlock({
            top: e.offsetY,
            left: e.offsetX,
            component: dragData.current?.dragComponent!,
          }),
        ]);

        setTimeout(() => {
          dragend.emit();
        });
      }) as any,
    };

    return block;
  })();
  //
  const focusHandle = (() => {
    const block = (e: MouseEvent<HTMLDivElement>, block: ReactVisualEditorBlock, index: number) => {
      if (preview) {
        return;
      }
      //shift,block
      if (e.shiftKey) {
        if (focusData.focus.length <= 1) {
          block.focus = true;
        } else {
          block.focus = !block.focus;
        }
        methods.updateBlocks(props.value.blocks);
      } else {
        // if (!block.focus) {
        //   block.focus = true;
        //   method.clearFocus(block);
        // }
        block.focus = !block.focus;
        methods.clearFocus(block);
      }
      setSelectIndex(block.focus ? index : -1);
      setTimeout(() => {
        blockDraggier.mousedown(e, block);
      });
    };
    const container = (e: MouseEvent<HTMLDivElement>) => {
      if (preview) {
        return;
      }
      if (e.target !== e.currentTarget) return;

      if (!e.shiftKey) {
        methods.clearFocus();
        setSelectIndex(-1);
      }
    };

    return {
      block,
      container,
    };
  })();

  //
  const blockDraggier = (() => {
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const [mark, setMark] = useState({
      x: null as null | number,
      y: null as null | number,
    });
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const dragData = useRef({
      startX: 0,
      startY: 0,
      startLeft: 0,
      startTop: 0,
      moveX: 0,
      moveY: 0,
      body: {
        startScrollTop: 0,
        moveScrollTop: 0,
      },
      startPosArray: [] as { top: number; left: number }[],
      dragging: false, //
      markLines: {
        x: [] as { left: number; showLeft: number }[],
        y: [] as { top: number; showTop: number }[],
      },
    });

    // eslint-disable-next-line react-hooks/rules-of-hooks
    const mousedown = useCallbackRef((e: MouseEvent<HTMLDivElement>, block: ReactVisualEditorBlock) => {
      document.addEventListener("mousemove", mousemove);
      document.addEventListener("mouseup", mouseup);
      document.addEventListener("scroll", scroll);
      dragData.current = {
        startX: e.clientX,
        startY: e.clientY,
        startLeft: block.left,
        startTop: block.top,
        startPosArray: focusData.focus.map(({ top, left }) => ({ top, left })),
        dragging: false,
        moveX: e.clientX,
        moveY: e.clientY,
        body: {
          startScrollTop: bodyRef.current.scrollTop,
          moveScrollTop: bodyRef.current.scrollTop,
        },
        markLines: (() => {
          const x: { left: number; showLeft: number }[] = [];
          const y: { top: number; showTop: number }[] = [];
          const { unFocus } = focusData;
          unFocus.forEach((b) => {
            y.push({ top: b.top, showTop: b.top }); //
            y.push({ top: b.top + b.height / 2 - block.height / 2, showTop: b.top + b.height / 2 }); //
            y.push({ top: b.top + b.height - block.height, showTop: block.top + block.height }); //
            y.push({ top: b.top - block.height, showTop: b.top }); //
            y.push({ top: b.top + b.height, showTop: b.top + b.height }); //

            x.push({ left: b.left, showLeft: b.left }); //
            x.push({ left: b.left + b.width / 2 - block.width / 2, showLeft: b.left + b.width / 2 }); //
            x.push({ left: b.left + b.width - block.width, showLeft: block.left + block.width }); //
            x.push({ left: b.left - block.width, showLeft: b.left }); //
            x.push({ left: b.left + b.width, showLeft: b.left + b.width }); //
          });
          return { x, y };
        })(),
      };
    });
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const handleMove = useCallbackRef((e) => {
      if (!dragData.current.dragging) {
        dragData.current.dragging = true;
        dragstart.emit();
      }

      let { startX, startY, moveX, moveY, body, startPosArray, markLines, startLeft, startTop } = dragData.current;
      moveY = moveY + body.moveScrollTop - body.startScrollTop;

      if (e.shiftKey) {
        if (Math.abs(moveX - startX) > Math.abs(moveY - startY)) {
          moveY = startY;
        } else {
          moveX = startX;
        }
      }
      const now = {
        mark: {
          x: null as null | number,
          y: null as null | number,
        },
        top: startTop + moveY - startY,
        left: startLeft + moveX - startX,
      };
      for (let i = 0; i < markLines.y.length; i++) {
        const { top, showTop } = markLines.y[i];
        if (Math.abs(now.top - top) < 5) {
          moveY = top + startY - startTop;
          now.mark.y = showTop;
        }
      }
      for (let i = 0; i < markLines.x.length; i++) {
        const { left, showLeft } = markLines.x[i];
        if (Math.abs(now.left - left) < 5) {
          moveX = left + startX - startLeft;
          now.mark.x = showLeft;
        }
      }
      const durX = moveX - startX,
        durY = moveY - startY;
      focusData.focus.forEach((block, index) => {
        const { left, top } = startPosArray[index];
        block.left = left + durX;
        block.top = top + durY;
      });

      methods.updateBlocks(props.value.blocks);
      setMark(now.mark);
    });
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const mousemove = useCallbackRef((e: MouseEvent<HTMLDivElement>) => {
      dragData.current.moveX = e.clientX;
      dragData.current.moveY = e.clientY;
      handleMove(e);
    }) as any;
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const mouseup = useCallbackRef((e: MouseEvent<HTMLDivElement>) => {
      document.removeEventListener("mousemove", mousemove);
      document.removeEventListener("mouseup", mouseup);
      document.removeEventListener("scroll", scroll);
      setMark({ x: null, y: null });
      if (dragData.current.dragging) {
        dragend.emit();
      }
    }) as any;
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const scroll = useCallbackRef((e: MouseEvent) => {
      dragData.current.body.moveScrollTop = (e.target as HTMLElement).scrollTop;
      handleMove(e);
    }) as any;
    return {
      mousedown,
      mousemove,
      mark,
    };
  })();

  const resizeDraggier = (() => {
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const dragData = useRef({
      startX: 0,
      startY: 0,
      dragging: false,
      direction: {
        horizontal: BlockResizeDirection.start,
        vertical: BlockResizeDirection.start,
      },
      block: {} as ReactVisualEditorBlock,
      startWidth: 0,
      startHeight: 0,
      startLeft: 0,
      startTop: 0,
    });

    // eslint-disable-next-line react-hooks/rules-of-hooks
    const mousedown = useCallbackRef((e: React.MouseEvent<HTMLDivElement>, direction: { horizontal: BlockResizeDirection; vertical: BlockResizeDirection }, block: ReactVisualEditorBlock) => {
      e.stopPropagation();
      e.preventDefault();
      document.body.addEventListener("mousemove", mousemove);
      document.body.addEventListener("mouseup", mouseup);
      dragData.current = {
        startX: e.clientX,
        startY: e.clientY,
        startWidth: block.width,
        startHeight: block.height,
        startLeft: block.left,
        startTop: block.top,
        dragging: false,
        direction,
        block,
      };
    });
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const mousemove = useCallbackRef((e: MouseEvent) => {
      let { startX, startY, startWidth, startHeight, direction, startLeft, startTop, dragging, block } = dragData.current;
      if (!dragging) {
        dragData.current.dragging = true;
        dragstart.emit();
      }
      let { clientX: moveX, clientY: moveY } = e;
      if (direction.horizontal === BlockResizeDirection.center) {
        moveX = startX;
      }
      if (direction.vertical === BlockResizeDirection.center) {
        moveY = startY;
      }
      let durX = moveX - startX;
      let durY = moveY - startY;

      if (direction.vertical === BlockResizeDirection.start) {
        durY = -durY;
        block!.top = startTop - durY;
      }
      if (direction.horizontal === BlockResizeDirection.start) {
        durX = -durX;
        block!.left = startLeft - durX;
      }

      const width = startWidth + durX;
      const height = startHeight + durY;
      block!.width = width;
      block!.height = height;
      block!.hasResize = true;
      methods.updateBlocks(props.value.blocks);
    }) as any;
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const mouseup = useCallbackRef((e: MouseEvent) => {
      document.body.removeEventListener("mousemove", mousemove);
      document.body.removeEventListener("mouseup", mouseup);
      if (dragData.current.dragging) {
        dragend.emit();
      }
    }) as any;

    return {
      mousedown,
    };
  })();

  //block
  const focusData = useMemo(() => {
    const focus: ReactVisualEditorBlock[] = [];
    const unFocus: ReactVisualEditorBlock[] = [];
    props.value.blocks.forEach((block) => {
      (block.focus ? focus : unFocus).push(block);
    });
    return { focus, unFocus };
  }, [props.value.blocks]);
  //
  const methods = {
    //block
    updateBlocks: (blocks: ReactVisualEditorBlock[]) => {
      props.onChange({
        ...props.value,
        blocks: [...blocks],
      });
    },
    updateValue: (value: ReactVisualEditorValue) => {
      props.onChange({ ...value });
    },
    clearFocus: (external?: ReactVisualEditorBlock) => {
      (!!external ? focusData.focus.filter((item) => item !== external) : focusData.focus).forEach((block) => {
        block.focus = false;
      });

      methods.updateBlocks(props.value.blocks);
    },
    showBlockData: (block: ReactVisualEditorBlock) => {
      $$dialog.textarea(JSON.stringify(block), { title: "", editReadonly: true });
    },
    importBlockData: async (block: ReactVisualEditorBlock) => {
      const text = await $$dialog.textarea("", { title: "Json" });
      try {
        const data = JSON.parse(text || "");
        commander.updateBlock(data, block);
      } catch (e) {
        console.error(e);
        notification.open({ message: "json" });
      }
    },
  };
  //
  const commander = useVisualCommand({
    value: props.value,
    focusData,
    updateBlocks: methods.updateBlocks,
    updateValue: methods.updateValue,
    dragstart,
    dragend,
  });
  const button: {
    label: string | (() => string);
    icon: ReactNode | (() => ReactNode);
    tip?: string | (() => string);
    handler: () => void;
  }[] = [
    {
      label: "",
      icon: <RollbackOutlined />,
      handler: commander.undo,
      tip: "ctrl+z",
    },
    {
      label: "",
      icon: <RedoOutlined />,
      handler: commander.redo,
      tip: "ctrl+y, ctrl+shift+z",
    },
    {
      label: () => (preview ? "" : ""),
      icon: () => (preview ? <EditOutlined /> : <EyeOutlined />),
      handler: () => {
        if (!preview) {
          methods.clearFocus();
        }
        setPreview(!preview);
      },
    },
    {
      label: "",
      icon: <ImportOutlined />,
      handler: async () => {
        $$dialog.textarea(JSON.stringify(props.value), { editReadonly: true, title: "JSON" });
        const text = await $$dialog.textarea("", {
          title: "JSON",
        });
        try {
          const data = JSON.parse(text || "");
          commander.updateValue(data);
        } catch (e) {
          notification.open({
            message: "",
            description: "",
          });
        }
      },
    },
    {
      label: "",
      icon: <ExportOutlined />,
      handler: () => $$dialog.textarea(JSON.stringify(props.value), { editReadonly: true, title: "JSON" }),
    },
    {
      label: "",
      icon: <VerticalAlignTopOutlined />,
      handler: () => {
        commander.placeTop();
      },
      tip: "ctrl+up",
    },
    {
      label: "",
      icon: <VerticalAlignBottomOutlined />,
      handler: () => {
        commander.placeBottom();
      },
      tip: "ctrl+down",
    },
    {
      label: "",
      icon: <DeleteOutlined />,
      handler: commander.delete,
      tip: "ctrl+d, backspace, delete",
    },
    {
      label: "",
      icon: <RestOutlined />,
      handler: commander.clear,
    },
    {
      label: "",
      icon: <CloseCircleOutlined />,
      handler: () => {
        methods.clearFocus();
        setEditing(false);
      },
    },
  ];

  const handler = {
    onContextmenu: (e: React.MouseEvent<HTMLDivElement>, block: ReactVisualEditorBlock) => {
      if (preview) return;
      e.preventDefault();
      e.stopPropagation();

      $$dropdown({
        reference: e.nativeEvent,
        render: () => (
          <>
            <DropdownOption label="" icon={<VerticalAlignTopOutlined />} onClick={commander.placeTop} />
            <DropdownOption label="" icon={<VerticalAlignBottomOutlined />} onClick={commander.placeBottom} />
            <DropdownOption label="" icon={<DeleteOutlined />} onClick={commander.delete} />
            <DropdownOption label="" icon={<EyeOutlined />} {...{ onClick: () => methods.showBlockData(block) }} />
            <DropdownOption label="" icon={<ImportOutlined />} {...{ onClick: () => methods.importBlockData(block) }} />
          </>
        ),
      });
    },
  };
  return (
    <div className={classes}>
      <div className="react-visual-editor-menu">
        {props.config.componentArray.map((component, index) => (
          <div className="react-visual-editor-menu-item" key={index} draggable onDragStart={(e) => menuDragger.dragStart(e, component)} onDragEnd={menuDragger.dragend}>
            {component.preview()}
            <div className="react-visual-editor-menu-item-name">{component.name}</div>
          </div>
        ))}
      </div>
      <div className="react-visual-editor-head">
        {button.map((btn, index) => {
          const label = typeof btn.label === "function" ? btn.label() : btn.label;
          const icon = typeof btn.icon === "function" ? btn.icon() : btn.icon;
          return (
            <div className="react-visual-editor-head-btn" key={index} onClick={btn.handler}>
              <span> {icon}</span>
              <span>{label}</span>
            </div>
          );
        })}
      </div>
      <ReactVisualEditorOperator selectBlock={selectBlock} value={props.value} updateValue={commander.updateValue} updateBlock={commander.updateBlock} config={props.config}></ReactVisualEditorOperator>
      <div className="react-visual-editor-body" ref={bodyRef}>
        <div
          className="react-visual-editor-container"
          style={containerStyle}
          ref={containerRef}
          onMouseDown={focusHandle.container}
          onContextMenu={(e) => {
            e.preventDefault();
          }}
        >
          {props.value.blocks.map((block, index) => (
            <ReactVisualBlock
              key={index}
              block={block}
              config={props.config}
              onMouseDown={(e) => {
                return focusHandle.block(e, block, index);
              }}
              onContextMenu={(e) => {
                handler.onContextmenu(e, block);
              }}
              formData={props.formData}
              onFormDataChange={props.onFormDataChange}
            >
              {block.focus && !!props.config.componentMap[block.componentKey] && ((!!props.config.componentMap[block.componentKey].resize && props.config.componentMap[block.componentKey].resize!.width) || props.config.componentMap[block.componentKey].resize!.height) && <ReactVisualBlockResize component={props.config.componentMap[block.componentKey]} onMouseDown={(e, direction) => resizeDraggier.mousedown(e, direction, block)}></ReactVisualBlockResize>}
            </ReactVisualBlock>
          ))}
          {blockDraggier.mark.y != null && <div className="react-visual-editor-mark-line-y" style={{ top: `${blockDraggier.mark.y}px` }} />}
          {blockDraggier.mark.x != null && <div className="react-visual-editor-mark-line-x" style={{ left: `${blockDraggier.mark.x}px` }} />}
        </div>
      </div>
    </div>
  );
};
