import { createVisualConfig } from "./utils/editorUtils";
import { Button, Input, Select } from "antd";
import { createColorProp, createSelectProp, createTableProp, createTextProp } from "./utils/ReactVisualEditor.props";

export const visualConfig = createVisualConfig();

visualConfig.registerComponent("text", {
  name: "",
  preview: () => <span></span>,
  render: ({ props }) => <span style={{ color: !props.color ? "" : props.color.hex, fontSize: props.size }}></span>,
  resize: {
    width: false,
    height: false,
  },
  props: {
    text: createTextProp(""),
    color: createColorProp(""),
    size: createSelectProp("", [
      { label: "14px", value: "14px" },
      { label: "18px", value: "18px" },
      { label: "24px", value: "24px" },
    ]),
  },
});

visualConfig.registerComponent("button", {
  name: "",
  preview: () => <Button type="primary"></Button>,
  render: ({ size, props }) => (
    <Button type={props.type || "primary"} style={size} size={props.size}>
      {props.text || ""}
    </Button>
  ),
  resize: {
    width: true,
    height: true,
  },
  props: {
    text: createTextProp(""),
    type: createSelectProp("", [
      { label: "", value: "default" },
      { label: "", value: "primary" },
      { label: "", value: "ghost" },
      { label: "", value: "dashed" },
      { label: "", value: "link" },
      { label: "", value: "text" },
    ]),
    size: createSelectProp("", [
      { label: "", value: "large" },
      { label: "", value: "middle" },
      { label: "", value: "small" },
    ]),
  },
});

visualConfig.registerComponent("input", {
  name: "",
  preview: () => <Input />,
  render: ({ size, model }) => <Input style={size} value={model?.default.value} onChange={model?.default.onChange} />,
  resize: {
    width: true,
  },
  model: {
    default: "",
  },
});

visualConfig.registerComponent("select", {
  name: "",
  preview: () => (
    <Select>
      <Select.Option value=""> </Select.Option>
    </Select>
  ),
  render: ({ props, size }) => (
    <Select style={{ width: size.width || "225px" }} key={(props.options || []).map(({ label, val }: { label: string; val: string }) => `${label}_${val}`).join(".")}>
      {(props.options || []).map((opt: any, index: number) => (
        <Select.Option value={opt.val} key={index}>
          {opt.label}
        </Select.Option>
      ))}
    </Select>
  ),
  props: {
    options: createTableProp("", "label", [
      { name: "", field: "label" },
      { name: "", field: "val" },
      { name: "", field: "comments" },
    ]),
  },
  resize: {
    width: true,
  },
});
